/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

main {
  flex-grow: 1; /* Hace que el contenido principal ocupe todo el espacio disponible */
}

*, *:before, *:after {
  box-sizing: inherit;
}

a {
  text-decoration: none;
  color: #1a1b15;
}

img,
picture {
  width: 100%;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0 2.5rem 0;
  font-weight: 700;
  color: #001F5B;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

h4 {
  font-size: 2.6rem;
  text-align: center;
}

p {
  color: #001F5B;
  font-size: 1.5rem;
  margin: 1rem 0 1rem 0;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 80rem;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.alinear-centro {
  text-align: center;
}

.w-100 {
  width: 100%;
}

.admin {
  margin-top: 4rem;
  width: 90%;
  border-spacing: 0;
  text-align: center;
}
.admin thead {
  background-color: #001F5B;
}
.admin thead th {
  color: #FFFFFF;
  padding: 2rem;
}
.admin thead tr {
  text-align: center;
}
.admin .imagen-tabla {
  width: 10rem;
}

.imagen-small {
  width: 20rem;
}

.agregarAnfitrion {
  background-color: #001F5B;
  padding: 0.5rem 1rem;
  color: #FFFFFF;
  font-size: 1.5rem;
  font-weight: 400;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: inline-block;
  margin: 0 0 3rem 0;
}
.agregarAnfitrion:hover {
  background-color: #000e28;
  cursor: pointer;
}

.btnNavegacion:hover {
  color: #001F5B;
}

.space-between {
  justify-content: space-between;
}

.flex-container {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
}

.flex-end {
  justify-content: flex-end;
}

.space-evenly {
  justify-content: space-evenly;
}

ul, ol {
  list-style: none;
}

.nav li a {
  background-color: #001F5B;
  color: #FFF;
  text-decoration: none;
  padding: 10px 15px;
  display: flex;
}

.nav li a:hover {
  background-color: #434343;
}

.nav > li {
  float: left;
}

.nav li ul {
  display: none;
  position: absolute;
  min-width: 200px;
}

.nav li:hover > ul {
  display: block;
}

.nav li ul li {
  position: relative;
}

.nav li ul li ul {
  right: -200px;
  top: 0px;
  background-color: yellow;
}

.controls {
  width: 50%;
  background: white;
  padding: 10px;
  border-radius: 16px;
  margin-bottom: 10px;
  border: 1px solid black;
  font-family: Fragment Core;
  font-size: 16px;
}

.controls2 {
  width: 100%;
  background: white;
  padding: 10px;
  border-radius: 160px;
  margin-bottom: 10px;
  border: 1px solid black;
  font-family: Fragment Core;
  font-size: 16px;
}

.botons {
  width: 100%;
  background: #fff;
  border-radius: 160px;
  padding: 10px;
  color: #001F5B;
  margin: 10px 0;
  font-size: 16px;
  font-family: lato;
}

.etiqueta {
  font-family: lato;
  font-size: 16px;
  color: #001F5B;
}

.botons {
  transition: transform 0.2s;
}

.botons:hover {
  transform: scale(1.5);
}

.no-webp .inicio {
  background-image: url(../img/header.jpg);
}

.webp .inicio {
  background-image: url(../img/header.webp);
}

.header {
  background-color: #323C4B;
  text-align: center;
}
.header.inicio {
  padding: 1rem 0 3rem 0;
  background-position: center center;
  background-size: cover;
}
@media (min-width: 768px) {
  .header.inicio {
    min-height: 70rem;
  }
}
@media (min-width: 768px) {
  .header.inicio .contenido-header {
    min-height: 70rem;
  }
}

.contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.footer {
  background-color: #323C4B;
  margin: 4rem 0 0 0;
  width: 100%;
  height: 6rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
.footer p {
  color: #FFFFFF;
  text-transform: uppercase;
  margin: 0;
}

.contenedor-propiedades {
  margin: 0;
  display: grid;
  gap: 5rem;
  grid-template-columns: repeat(2, 1fr);
}
@media (min-width: 768px) {
  .contenedor-propiedades {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(3, 1fr);
  }
  .contenedor-propiedades img {
    width: 30rem;
    height: 15rem;
  }
}

.propiedad img {
  width: 30rem;
  height: 15rem;
}

@media (min-width: 768px) {
  .perfil-anfitrion {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.perfil-anfitrion .tabla-izq {
  text-align: center;
  margin: 2rem 0 5rem 0;
}
.perfil-anfitrion .tabla-izq p {
  margin: 0.6rem 0;
}
.perfil-anfitrion .tabla-der {
  text-align: center;
  margin: 2rem 0 5rem 0;
}
.perfil-anfitrion .tabla-der .kchat {
  font-size: 2.5rem;
  margin: 0.6rem 0;
}

.thead-izq td {
  width: auto;
  padding: 2rem;
}

.generarPDF {
  background-color: #001F5B;
  padding: 0.5rem 1rem;
  color: #FFFFFF;
  font-size: 2rem;
  font-weight: 400;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  margin: 0 auto;
}
.generarPDF:hover {
  background-color: #000e28;
  cursor: pointer;
}

.barra {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
  }
}

.derecha {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .derecha {
    align-items: flex-end;
  }
}

.navegacion {
  visibility: hidden;
  opacity: 0;
  height: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.5s linear, height 0.5s linear;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    align-items: center;
    opacity: 1;
    visibility: visible;
    margin-right: 8rem;
  }
}
.navegacion a {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 2.2rem;
  display: block;
  text-align: center;
}
.navegacion a:hover {
  color: #001F5B;
}
@media (min-width: 768px) {
  .navegacion a {
    margin-right: 2rem;
    font-size: 1.8rem;
  }
  .navegacion a:last-of-type {
    margin-right: 0;
  }
}

/** Menu de hamburguesa**/
@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  width: 5rem;
}
.mobile-menu img:hover {
  cursor: pointer;
}

.mostrar {
  visibility: visible;
  opacity: 1;
  height: auto;
}

.formulario p {
  font-size: 1.4rem;
  color: #333333;
  margin: 2rem 0 0 0;
}
.formulario legend {
  font-size: 2rem;
  color: #333333;
}
.formulario label {
  font-weight: 700;
  text-transform: uppercase;
  display: block;
}
.formulario input:not([type=submit]),
.formulario textarea,
.formulario select {
  padding: 1rem;
  display: block;
  width: 100%;
  background-color: #FFFFFF;
  border: 1px solid #e1e1e1;
  border-radius: 1rem;
  margin-bottom: 2rem;
}
.formulario textarea {
  height: 20rem;
}

