.alerta {
  padding: 1rem;
  width: 100%;
  display: block;
  margin-bottom: 1rem;
  text-align: center;
  font-weight: 700;
  text-transform: uppercase;
}
.alerta.error {
  color: #cb0000;
}
.alerta.exito {
  color: #329f00;
}

table {
  font-family: "Lucida Sans Unicode", "Lucida Grande", Sans-Serif;
  margin-top: 4rem;
  width: 90%;
  border-spacing: 0;
  text-align: center;
  margin: 0 auto;
}

thead {
  background-color: #001F5B;
}
thead th {
  color: #FFFFFF;
  padding: 1.5rem;
  font-size: 1.3rem;
  border-top: 0.4rem solid #aabcfe;
  border-bottom: 0.2rem solid #FFFFFF;
  text-transform: uppercase;
}

tr {
  text-align: center;
}
tr:hover td {
  background: #d0dafd;
  color: #339;
}

td {
  background-color: #e8edff;
  border-bottom: 0.1rem solid #FFFFFF;
  font-size: 1.1rem;
}

.contenedor-select {
  display: flex;
  justify-content: flex-end;
  margin-right: 5.3rem;
}
.contenedor-select select {
  padding: 0.8rem;
  width: 20%;
  border: 1px solid #e1e1e1;
  border-radius: 1rem;
  margin: 1rem 0.5rem;
  font-weight: 300;
}